<template>
    <div class="register">
        <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px"
                 class="demo-ruleForm" label-position="top">
            <el-form-item label="" prop="name">
                <el-input v-model.number="ruleForm2.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="pass">
                <el-input type="password" v-model="ruleForm2.pass" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="checkPass">
                <el-input type="password" v-model="ruleForm2.checkPass" autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm2')"></el-button>
                <el-button @click="resetForm('ruleForm2')"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import {mapActions} from 'vuex';
    const reg = /^\d+/;
    export default {
        name: "Register",
        data() {
            var checkName = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error(''));
                } else if (reg.test(value)) {
                    return callback(new Error(''));
                } else {
                    callback();
                }
            };
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if (value.length < 6 || value.length > 16) {
                    callback(new Error('6~16'));
                } else {
                    if (this.ruleForm2.checkPass !== '') {
                        this.$refs.ruleForm2.validateField('checkPass');
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if (value !== this.ruleForm2.pass) {
                    callback(new Error('!'));
                } else {
                    callback();
                }
            };
            return {
                message: '',
                ruleForm2: {
                    name: '',
                    pass: '',
                    checkPass: ''
                },
                rules2: {
                    name: [
                        {validator: checkName, trigger: 'blur'}
                    ],
                    pass: [
                        {validator: validatePass, trigger: 'blur'}
                    ],
                    checkPass: [
                        {validator: validatePass2, trigger: 'blur'}
                    ]
                }
            };
        },
        methods: {
            ...mapActions(['register']),
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.register({username: this.ruleForm2.name, password: this.ruleForm2.pass})
                            .then(res => {
                                this.$notify({
                                    type: 'success',
                                    title: '',
                                    message: ' ()',
                                    offset: 100
                                });
                                this.$router.push('/');
                            }, err => {
                                this.$message.error('');
                            });
                    } else {
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style scoped>
    .register {
        min-width: 500px;
    }
</style>